.site-header {
    background-color: #f5f5f5; }

.img-responsive {
    display: block;
    width: 100%;
    height: auto;
    &.img-width {
        width: 100%; } }

.post {
    margin-bottom: 30px;
    &-image {
        position: relative;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat; } }


.site-nav-custom {
    z-index: 10;
    border: none;
    background-color: #f5f5f5; }



.article-ad {
    margin: 30px 0; }


.pagination {
    display: block;
    position: relative;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    li {
        display: inline-block;
        position: relative;
        border: 1px solid #111;
        padding: 5px 10px;
        list-style: none;
        transition: background-color 0.8s ease;
        a {
            font-size: 1em;
            color: #111;
            text-decoration: none; }
        &:hover {
            background-color: lighten(#828282, 30%); } }
    .active {
        background-color: #828282; } }

.post-list {
    li {
        border-bottom: 1px solid #111;
        padding-bottom: 30px;
        // box-shadow: 0 0 10px rgba(0,0,0,0.5)
        &:last-of-type {
            border: none; } } }

.post-category {
    text-transform: uppercase;
    color: red;
    border: 1px solid red;
    display: inline-block;
    padding: 3px;
    font-size: 16px; }
.wrapper {
    max-width: 1170px; }

.tags {
    display: inline-block;
    padding: 5px;
    background-color: #828282;
    border-radius: 7%;
    line-height: 1.0;
    margin: 3px 3px;
    a {
        color:  #ffffff; } }

.likely {
    &-post {
        margin-bottom: 20px; } }

